<template>
  <div class="logo-container rel">
    <div class="abs logo-animate persp">
      <img class="one abs" src="https://www.dl.dropboxusercontent.com/s/9nsk8xhh0ctmi0m/bxb_b_vector_3.svg" type="image/svg+xml">
      <img class="two abs" src="https://www.dl.dropboxusercontent.com/s/m11i53t5i0q6y0r/bxb_b_vector_2.svg" type="image/svg+xml">
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  props: [],
  components: {
    // '': require('@/components').default
  },
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {

    }
  }
}
</script>

<style lang="less" scoped>
.persp{
  perspective: 2000px;
  perspective-origin: 91rem 272rem;
  transform-style: preserve-3d;
}
.logo-container{
  height: 100%;
}

.logo-animate {
    transform: translate(-50%, -50%) rotateZ(-25deg);
    top: 45%;
    left: 50%;
    z-index: 1;
    font-family: Saira;
    width: 250px;
    height: 250px;
    line-height: 100%;
    opacity: .4
}
.logo-animate img.one {
    filter: brightness(1.25);
    opacity:.8;
}
.logo-animate img.two {
    opacity: .6;
    transform: rotate3d(1, 0, 0, 21deg) scaleY(3.2);
}
.logo-animate img {
  width: 250px;
  height: 250px;
}
</style>



