      <content-loader :src="`WP${i+1}.md`"></content-loader>
    </section>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {
      work_plan: [
        "","",""
      ]
    }
  }
}
</script>
<style lang="less">
@media screen and (max-width: 900px){
  .work-plan{
    h3{
      width: calc(100% - 3.2em);
      transform: translateX(3.2em)
    }
  }
}
</style>
<style lang="less" scoped>
.wp{
  top:.2px; left: -3.2em;
  font-size: 27px;
  font-weight: 200;
  @media screen and (max-width: 900px){
    transform: translateX(3.2em)
  }
}

</style>



