<template>
  <div class="explore">
    <h1>Interstellar matter</h1>
    <section>
      <h2>
        Interstellar turbulence
      </h2>
      <div class="flex row">
        <img src="/static/content/explore/interstellar_matter/interstellar_turbulence.png"/>
        <p>
          Illustration of interstellar turbulence. The RGB colors represent maps of HI 21cm line emission in different velocity channel maps. The structure of matter and magnetic fields in space emerges from interstellar turbulence.  This is why interstellar matter is a main science topic of the BxB project.
        </p>
      </div>
    </section>
    <section>
      <h2>
        Towards a 3D view.
      </h2>
      <div class="flex row-reverse">
        <img src="/static/content/explore/interstellar_matter/3d_view.png"/>
        <p>
          The combination of the Gaia data with observational tracers of interstellar matter opens the possibility to map the density structure and magnetic fields in 3 dimensions. This is illustrated with a 3D image of matter in the Solar Neighborhood built combining stellar reddening  and parallaxes.
        </p>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {

    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
img{width: 42%;margin: 0 !important;}
p{
  width: 47%;
}
.flex{
  justify-content: space-between;
}
.explore{padding-bottom: 72px;}
</style>



