<style lang="less" scoped>
/* @import './assets/css/functional.css'; */


ul.logo-grid-container{
  display: flex;
  flex-wrap: wrap;
  li{
    width: 33.3%;
    height: 9vw;
    margin-bottom: 24px;
    &.anr{
