<template>
  <div class="explore">
    <h1>Interstellar dust</h1>
    <section class="interstellar_dust_models">
      <h2>
        Interstellar Dust Models
      </h2>
      <img src="/static/content/explore/interstellar_dust/dust_models.png"/>
      <div class="flex center">
        <svg class="arrow right" xmlns="http://www.w3.org/2000/svg" viewBox="-2 -2 83.37 56.24">
          <defs>
            <filter id="shadow">
              <feDropShadow dx="0" dy="0" stdDeviation=".5"/>
            </filter>
          </defs>
          <polygon class="cls-1" points="34.25 17.7 0 17.7 0 34.54 34.25 34.54 34.25 52.24 79.37 26.12 34.25 0 34.25 17.7"/>
        </svg>
        <p>
          Several dust components and emission processes may contribute to dust polarization at microwave frequencies.
        </p>
      </div>
    </section>
    <section class="grain_shape_composition">
      <h2>
        Grain shape and composition
      </h2>
      <div class="flex">
        <img src="/static/content/explore/interstellar_dust/grain_shape_composition.png"/>
        <p>
          Dust polarization depends on the dust grains shape and composition. This is illustrated here with a plot showing the ratio between dust polarization at sub-millilemter  to visible wavelengths computed for varying grain axis ratio and degrees of grain porosity.
        </p>
      </div>
    </section>
    </div>

  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {

    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
svg.arrow{
  width: 100px;
}
svg .cls-1 {
  fill: #53595f;
  filter:url(#shadow);
}
section.interstellar_dust_models{
  svg.arrow{
    margin-right: 24px;
  }
  p{
    width: 70%;
    margin: 0;
  }
}
section.grain_shape_composition{
  div.flex{
    margin-bottom: 24px;
  }
  img{
    width: 50%;
  }
  p{
    width: 37%;
    // text-align: left;
  }
}
</style>



