<template>
  <div class="explore">
    <h1>Magnetic fields</h1>
    <section>
      <h2>
        Tracing magnetic fields
      </h2>
      <div class="flex row">
        <img src="/static/content/explore/magnetic_fields/magnetized-iron-sand-woskay.png"/>
        <p>
A tracer is needed to image magnetic field lines. In this picture iron needles are aligned along magnetic fields lines.
        </p>
      </div>
      <div class="flex row-reverse">
        <img  src="/static/content/explore/magnetic_fields/interstellar-matter-filament.png"/>
        <p>
In space, interstellar dust particles align with interstellar magnetic field lines. Their emission is polarized and this polarization tells astronomers about the orientation of interstellar magnetic field lines.
        </p>
      </div>
    </section>
    <section>
      <h2>

      </h2>
      <div class="flex row">
        <img  src="/static/content/explore/magnetic_fields/magnetized-iron-needles.png"/>
        <p>
Magnetic fields create anisotropic structures. In this picture to the left, on earth, this is illustrated with iron needles.
        </p>
      </div>
      <div class="flex row-reverse">
        <img  src="/static/content/explore/magnetic_fields/polarization-around-orion.png"/>
        <p>
          In this picture to the right, in space, this is illustrated by the alignment of a filament of interstellar matter with the magnetic field orientation inferred from the dust polarization.
        </p>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {

    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
.flex{
  justify-content: space-between;
  margin-bottom: 72px;
}
.row-reverse{
  align-items: flex-end;
  margin-top: -14vw;
  p{
    margin-bottom: 47px;
  }
}
.row{
  p{
    margin-top: 24px;
  }
}
p{
  width: 47%;
}
img{
  width: 40%;
  margin: 0 !important;
}
</style>



