import Vue from 'vue'
import Router from 'vue-router'


Vue.use(Router)

const routes = [
  {
    path: '/',
    component: require('@/components/byPages/landing-page').default
  },
  {
    path: '/publications',
    component: require('@/components/byPages/publications').default
  },
  {
    path: '/team',
    component: require('@/components/byPages/team').default
  },
  {
    path: '/workplan',
    component: require('@/components/byPages/workplan').default
  },
  {
    path: '/events',
    component: require('@/components/byPages/events').default
  },
  {
    path: '/explore',
    component: require('@/components/byPages/explore').default
  },
  {
    path: '/explore/interstellar-matter',
    component: require('@/components/byPages/explore/interstellar-matter').default
  },
  {
    path: '/explore/interstellar-dust',
    component: require('@/components/byPages/explore/interstellar-dust').default
  },
  {
    path: '/explore/dust-polarization',
    component: require('@/components/byPages/explore/dust-polarization').default
  },
  {
    path: '/explore/cmb-polarization',
    component: require('@/components/byPages/explore/cmb-polarization').default
  },
  {
    path: '/explore/grain-alignement',
    component: require('@/components/byPages/explore/grain-alignement').default
  },
  {
    path: '/explore/interstellar-matter',
    component: require('@/components/byPages/explore/interstellar-matter').default
  },
  {
    path: '/explore/magnetic-fields',
    component: require('@/components/byPages/explore/magnetic-fields').default
  },
  {
    path: '*',
    component: require('@/components/base-components/error-404').default
  },
]


export default new Router({
  routes: routes
})



