<template>
  <div :class="$options.name" class="rel">
    <ul class="flex row">
      <li v-for="item in items">
        <carousel-item :item="item"></carousel-item>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "carousel",
  components: {
    'carousel-item': require('@/components/base-components/carousel-item').default
  },
  props: [],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  // computed: {},
  watch: {},
  methods: {},
  data () {
    return {
      items: [
        {
          src: "bxb_sky_ellipse_3.png",
          caption: `Polarised emission\nfrom milky way dust`
        },
        {
          src: "bxb_sky_ellipse_1.png",
          caption: `Polarisation of the\ncosmic microwave background`
        },
        {
          src: "bxb_sky_ellipse_2.png",
          caption: `30GHz\nMagnetic Field`
        },
        {
          src: "bxb_sky_ellipse_3.png",
          caption: `Polarised emission\nfrom milky way dust`
        },
      ]
    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
.carousel{
  height: 104vh;
  width: 100vw;
  right:0;
  animation: carousel-animation 10s linear;
  overflow: hidden;
  &:before{
    position: absolute; content: "";
    top:0; right:0; bottom:calc(12vh - 1vw); left: 0;
    background-color: #111;
    transform: rotate(-2.4deg) scale(1.1) ;
  }
}
ul{
  width: 400vw;
  animation: carouselSlide 12s infinite;
}
li{
  width: 100vw;
  height: 100vh;
  position: relative;
  z-index: 2;
  display: flex; align-items: center; justify-content: center;
}


@keyframes carouselSlide {
	0% { transform: translateX(0)}
  27% { transform: translateX(0)}
  33.3% { transform: translateX(-100vw)}
  60% { transform: translateX(-100vw)}
  66.6% { transform: translateX(-200vw)}
  93% { transform: translateX(-200vw)}
  99.9% { transform: translateX(-300vw)}
	100% { transform: translateX(-300vw)}
}
</style>



