<template>
  <div class="explore">
    <h1>Dust polarization</h1>
    <section>
      <h2>Polarization angle</h2>
      <img src="/static/content/explore/dust_polarization/polarization_angle.png"/>
      <p>
        A coherent pattern is clearly apparent on the map of polarization angles.
      </p>
      <p class="bold">
        It may trace a local deformation of the Galactic magnetic field associated with the Local Bubble.
      </p>
    </section>
    <section>
      <h2>Polarization fraction</h2>
      <img src="/static/content/explore/dust_polarization/polarization_fraction.png"/>
      <div class="flex">
        <img src="/static/content/explore/dust_polarization/planck_ism_clouds.png"/>
        <p>
          The structure from the Planck map traces a variable degree of depolarization from the superposition of a small number of ISM clouds, with distinct polarization orientations, along the line of sight.
        </p>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {

    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
p{
  text-indent: 0px;
}
section>div.flex{
  img{
    width: 40%;
  }
  p{
    width: 50%;
  }
}
</style>



