<template>
  <div class="" v-html="content">

  </div>
</template>
<script>
import marked from 'marked'
export default {
  name: "",
  components: {},
  props: [
    'src'
  ],
  // created(){},
  mounted(){
    this.$xhr.get(`/static/content/${this.src}`, (req)=>{
      // console.log(req.response)
      this.content = marked(req.response, {sanitize: true})
    }, null)
  },
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {
      content: ""
    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
</style>



