<template>
  <nav class="fix main full flex noselect" :class="{'dark-mode': ui.darkModeHeader}">
    <div class="mobile-nav" :class="{open: ui.mobileNav}">
      <div class="flex center mobile-nav-reveal"
      @click="ui.mobileNav = !ui.mobileNav">
        <svg class="icon chevron-down" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24">
          <polyline points="6 9 12 15 18 9 6 9"/>
        </svg>
      </div>
      <nav>
        <ul class="flex col">
          <li @click="handleMobileNavDirect">
            <router-link to="/explore">Explore</router-link>
          </li>
          <li @click="handleMobileNavDirect">
            <router-link to="/workplan">Work Plan</router-link>
          </li>
          <li @click="handleMobileNavDirect">
            <router-link to="/team">Team</router-link>
          </li>
          <li @click="handleMobileNavDirect">
            <router-link to="/publications">Publications</router-link>
          </li>
          <li @click="handleMobileNavDirect">
            <router-link to="/events">Events</router-link>
          </li>
        </ul>
      </nav>
    </div>
    <div class="bxb-project li"  @click="handleMobileNavDirect">
      <router-link to="/">BxB Project</router-link>
    </div>
    <ul class="full flex">
      <li>
        <router-link to="/explore">Explore</router-link>
      </li>
      <li>
        <router-link to="/workplan">Work Plan</router-link>
      </li>
      <li>
        <router-link to="/team">Team</router-link>
      </li>
      <li>
        <router-link to="/publications">Publications</router-link>
      </li>
      <li>
        <router-link to="/events">Events</router-link>
      </li>
    </ul>
    <div class="login li">
      <a href="" class="login flex center">
        <div class="login">
          login
        </div>
        <svg xmlns="http://www.w3.org/2000/svg" class="login icon" viewBox="0 0 20 20">
          <path class="cls-1"
          d="M8.1,19.82a10,10,0,0,1-4-17.89A1.33,1.33,0,0,1,6,2.42H6a1.35,1.35,0,0,1-.41,1.7,7.33,7.33,0,1,0,5.71-1.33V9.67A1.33,1.33,0,0,1,10,11h0A1.33,1.33,0,0,1,8.67,9.67V1.07A1.07,1.07,0,0,1,9.74,0h0c4.77,0,9.48,3.82,10.16,8.54A10,10,0,0,1,8.1,19.82Z"
          />
        </svg>
      </a>
    </div>
  </nav>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [],
  // created(){},
  // mounted(){},
  // beforeDestroy(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {
    handleMobileNavDirect: function(){
      console.log("yahe", this.ui.mobileNav)
      this.ui.mobileNav = false
      console.log(this.ui.mobileNav)
    }
  },
  data () {
    return {
      ui: {
        mobileNav: false,
        darkModeHeader: false,
      }
    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
@import '../../assets/styles/global.less';
nav.main{
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  padding: 8px;
  font-weight: 200;
  font-family: Saira;
  align-items: center;
  width: 100vw;
  height: @nav-height;
  background-color: rgba(255,255,255,.89);
  z-index: 5;
  @media screen and (max-width: 500px) {
    height: 60px;
  }
  // transition: background-color .27s;
}
nav.main.dark-mode{
  box-shadow: none;
  color: #fff;
  background-color: rgba(255,255,255,0);
  // box-shadow: 0 0 12px 2px rgba(0, 0, 0, 0.6);
  transition: background-color .27s;
  nav{
    background-color: #111 !important;
  }
  svg{
    fill: #fff !important;
  }
  // &:before{
  //   position: absolute; content: "";
  //   top:0; left: 0; right: 0; height: 72px;
  //   background-color: #111;
  //   z-index: -1;
  //
  // }
}

a{
  &.router-link-exact-active{
    border-color: @orange;
  }
}

nav.main>ul{
  align-items: center;
  justify-content: flex-start;
  overflow-x: hidden;
  @media screen and (max-width: 670px){
    width: 0 !important;
  }
}

nav.main>ul>li, nav .li{
  margin: 0 14px;
}
nav.main .li.bxb-project{
  flex-shrink: 0;
  @media screen and (max-width: 670px){
    transform: translateX(@nav-height)
  }
  @media screen and (max-width: 500px) {
    transform: translateX(@nav-height-mobile)
  }
}
nav .mobile-nav{
  position: fixed;
  top:0; left:0;
  pointer-events: none;
  opacity: 0;
  svg{
    width: 37px;
  }
  @media screen and (max-width: 670px){
    opacity: 1;
    pointer-events: all;
  }
  .mobile-nav-reveal{
    position: absolute;
    top:0; left:0;
    width: 52px;height: @nav-height;

    box-shadow: 1px 0 1px rgba(0,0,0,.3);
    @media screen and (max-width: 500px) {
      height: 60px;
      width: @nav-height-mobile;
    }
  }
  nav{
    height: 0;
    overflow-y: hidden;
    transition: none;
  }
  &.open{


    .mobile-nav-reveal{
      box-shadow: none;
      svg{
        transform: rotate(.5turn)
      }
    }
    nav{
      padding-bottom: 13px;
      ul{
      }
      transition: height .2s;
      height: 298px;
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.2);
      position: absolute;
      top:@nav-height; left:0;
      @media screen and (max-width: 500px) {
        top: 60px;
      }
      width: 100vw;
      background: #fff;

      li{
        margin-left: 94px;
        margin-top: 12px;
        margin-bottom: 12px;
      }
      li+li{
        margin-top: 24px;
      }
    }
  }
}

nav li.login, nav .li.login{
  margin-left: auto;
  a{
    min-height: 1em;
  }
  svg{
    height: .8em;
    stroke: none;
    fill: #1d1d1d;
  }
  div.login{
    margin-right: 8px;
  }
  @media screen and (max-width: 710px) {
    div.login{
      display: none;
    }
  }
}
</style>



