<template>
  <div class="carousel-item rel flex center">
    <div class="background-image" :style="{backgroundImage: `url(/static/img/${item.src})`}">
    </div>
    <div class="caption">{{item.caption}}</div>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [
    'item'
  ],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {

    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
.carousel-item{
  width: 90%;
  height: 70%;
}
.background-image{
  width: 100%;
  height: 100%;
  @media screen and (max-width: 640px){
    width: 90vh;
    transform: scale(1.3) translateY(calc(-4vh + 3vw)) rotate(90deg);
  }
  @media screen and (max-width: 420px){
    transform: scale(1.6) translateY(calc(-4vh + 3vw)) rotate(90deg);
  }
}
.caption{
  position: absolute;
  color: #fff;
  bottom:0; left:0;
  white-space: pre;
  text-align: left;
  margin-left: 5vw;
}

</style>



