<template>
  <div :class="$options.name" class="restrict-width">
  <content-loader src="team-page-intro.md"></content-loader>
  <ul>
    <li v-for="lab in peopleByLabs">
    <h4>{{lab.name}} ({{lab.acronym}})</h4>
    <ul class="emphase">
      <li v-for="people in lab.people">
        {{people}}
      </li>
    </ul>
    </li>
  </ul>
</div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [],
  beforeCreate(){
    this.$xhr.get('/static/content/labs.json', function(xhr){
      this.peopleByLabs = JSON.parse(xhr.responseText)
    }.bind(this))
  },
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {
      peopleByLabs: []
    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
ul.emphase li{
  font-family: Saira;
  font-size: 0.9em;
  // font-weight: 400;
}
</style>



