import Vue from 'vue'
import App from './App'
import router from './router'
Vue.config.productionTip = false

//
Vue.component('content-loader', require('@/components/base-components/globals/content-loader').default)

Vue.prototype.$xhr = {
  get: function(url, callback, errorcallback){
    const req = new XMLHttpRequest()
    req.onreadystatechange = ()=>{
      if (req.readyState === 4) {
        if (req.status === 200) {
          callback(req)
          // console.log("succes", req.responseText)
        } else {
          if (errorcallback){
            // errorcallback(xhr).bind(this)
          } else {
            console.log("xhr error", req.status, req.statusText)
          }
        }
      }
    }
    req.open('GET', url, true)
    req.send(null)
  },
  post: function(url, payload, callback, errorcallback){
    console.log("not defined yet")
  }
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



