<template>
  <div class="publication-reference rel">
    <a class="title" :href="refs.link">
      {{refs.title}}
    </a>
    <div class="journal">
      {{refs.journal}}
    </div>
    <ul class="authors">
      <li v-for="(author, index) in refs.authors">
        {{author}}
        <span v-if="index + 2 === refs.authors.length">and</span>
        <span v-else-if="index + 1 === refs.authors.length"></span>
        <span v-else>,</span>
      </li>
    </ul>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [
    'refs'
  ],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {

    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
.publication-reference{
  margin:24px 0 27px;
  ul.authors{
    li{
      display: inline
    }
    li+li{
      margin-left: 2px
    }
  }
  .title{
    color: #ff7830;
    font-weight: 500;
    letter-spacing: .8px;

  }
  .journal{
    font-size: .7em
  }
  .authors{
    font-family: Saira; font-weight: 400;
    font-size: .9em;
    font-weight: 400;

  }
}
</style>



