<template>
  <div class="explore cmb-polarization">
    <h1>CMB Polarization</h1>
    <section>
      <h2>Cosmic Perspective</h2>
      <img src="/static/content/explore/cmb_polarization/history_of_cosmic_magnetism.png"/>
    </section>
    <section class="planck_results rel">
      <h2>CMB Temperature & Polarisation</h2>
      <img src="/static/img/bxb_sky_ellipse_2.png"/>
      <img src="/static/content/explore/cmb_polarization/planck_satellite.png" class="abs planck_satellite"/>
      <p class="abs">
        from Planck results 2015
      </p>
    </section>
    <section class="power_spectra">
      <h2>CMB Power spectra</h2>
      <div class="flex row">
        <div class="flex col">
          <p>
E-mode (gradient-like) polarization validates the acoustic interpretation of temperature peaks and enhances the precision of cosmological parameters.
          </p>
          <p>
B-mode (curl-like) polarization from gravitational lensing is also detected.
          </p>
          <p>
No detection (yet) of the weaker B-mode signal expected from primordial gravitational waves.
          </p>
        </div>
        <img class="translate-up" src="/static/content/explore/cmb_polarization/cmb_power_spectra.png"/>
      </div>
    </section>
    <section>
      <h2>The Galactic foreground screen</h2>
      <div class="flex row">
        <div class="flex col">
          <p>
            The B-mode signal expected from primordial gravitational waves is smaller than the corresponding signal from the Galaxy everywhere on the sky.
          </p>
          <svg class="arrow-down" xmlns="http://www.w3.org/2000/svg" viewBox="-2 -2 56.24 83.37">
            <defs>
              <filter id="shadow">
                <feDropShadow dx="0" dy="0" stdDeviation=".5"/>
              </filter>
            </defs>
            <polygon class="cls-1" points="34.54 34.25 34.54 0 17.7 0 17.7 34.25 0 34.25 26.12 79.37 52.24 34.25 34.54 34.25"/>
          </svg>

          <p>
            Any claim for a detection will face a critical assessment against alternative interpretations involving foregrounds.
          </p>
        </div>
        <img class="translate-up" src="/static/content/explore/cmb_polarization/keck_field.png"/>
      </div>
    </section>
    <section class="conclusion">
      <p>
        The power spectra of dust polarization was characterized over the whole sky using Planck data.
      </p>
      <p>
        There is no sky area where the Galactic signal may be neglected.
      </p>
      <p class="bold">
        Any claim for a detection will face a critical assessment against alternative interpretations involving foregrounds.
      </p>
    </section>
  </div>
</template>
<script>
export default {
  name: "",
  components: {},
  props: [],
  // created(){},
  // mounted(){},
  // updated(){},
  // activated(){}
  computed: {},
  watch: {},
  methods: {},
  data () {
    return {

    }
  }
}
</script>
<style></style>
<style lang="less" scoped>
img.planck_satellite{
  bottom: 0;
  left: 0;
  width: 14%;
  transform: rotate(32deg)
}
p.abs{
  bottom: 0;
  right: 0;
}
section.power_spectra{
  div.flex.row{
    .flex.col{
      width: 50%;
      p{
        margin-bottom: 24px;
      }
    }
    img{
      width: 50%;

    }
  }
}
img.translate-up{
  transform: translateY(calc(-24px - 2vw);)
}
svg.arrow-down{
  .cls-1 {
    fill: #53595f;
    filter:url(#shadow);
  }
  height: 72px;
  margin: 24px 0;
}

section.conclusion{
  p{
    margin-bottom: 36px;
  }
}
</style>



