<template>
  <div class="explore">
    <h1>Grain Alignement</h1>
    <section>
      <div class="flex row">
        <div class="flex col p-col">
          <p>
Interstellar grains spin like tops around their axis of largest moment of inertia. To be aligned they must spin at high (supra-thermal) rotation velocities.
          </p>
          <p>
Spinning dust grains acquire a magnetic moment aligned with their spin axis (Barnett effect), which interacts with interstellar magnetic fields. As a result, the grain rotation axis precesses around interstellar magnetic field lines.
          </p>
          <p>
It is thought that radiative torques and paramagnetic relaxation contribute both to align the grain magnetic moment with interstellar field lines.
          </p>
        </div>
        <img src="/static/content/explore/grain_alignement/grain_magnetic_alignement.png"/>
      </div>
    </section>
  </div>
</template>
<script>
